import { Typography } from '@mui/material'
import React from 'react'
import './Profile.css'
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';

const Profile = () => {
    return (
        <div className="ProFile containerShadow">
            <div className="ProfileName">
                <Typography className="Name">Worapon Boonchoo</Typography>
                <Typography className="NickName">Dear</Typography>
            </div>
            <figure className="ProfileImage">
                <img src={require('../assets/images/Profile.png').default}  alt="myPortfolio"/>
            </figure>
            <div className="ProfileDetail">
                <Typography><span>Name </span>: Worapon</Typography>
                <Typography><span>SurName </span>: Boonchoo</Typography>
                <Typography><span>Gender </span>: Male</Typography>
                <Typography><span>Age </span>: 22</Typography>
                <div className="ButtonContainer">
                <button variant="outlined">Dowload My Resume</button>
                </div>
            </div>
        </div>
    )
}

export default Profile
