import { Grid, Typography } from '@mui/material'
import React from 'react'
import ResumeData from '../../utils/ResumeData'
import './Resume.css'
import { TimelineDot,Timeline,TimelineItem,TimelineSeparator,TimelineConnector,TimelineContent} from '@material-ui/lab';
import { Paper } from '@material-ui/core'
import TimelineCustom from './TimelineCustom';



const Resume = () => {
    return (
        <>
        {/*About Me */}
        <Grid container className="section padbuttom" >
            <Grid item className="title">
                <span></span>
                <h6 className="titleText">About Me</h6>
            </Grid>
            <Grid item className="title" xs={12}>
                <Typography className="aboutMe">
                    Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum
                </Typography>
            </Grid>

        </Grid>
        {/*Education */}
        <Grid container className="section">
            <Grid item >
                <TimelineCustom/>
            </Grid>
            
        </Grid>
        {/*Skills */}
        <Grid container spacing={3} justify="space-between" className="section">
            {ResumeData.skills.map((skill)=>(
                <Grid item xs={12}  md={4} sm={3} >
                    <Paper elevation={2} className="skill">
                        <Typography variant="h6" className="skillTitle">
                            {skill.title}
                        </Typography>
                        {skill.description.map((element)=>(
                            <Typography variant="body2" className="skillDescription">
                                <TimelineDot variant={'outlined'} className="timelineDot"/> 
                                    {element}
                                
                           
                            </Typography>
                            
                        ))}

                        
                        
                    </Paper>
            </Grid>
            ))}
            
            
        </Grid>
        </>
    )
}

export default Resume
